.GraphViewer{
  flex: 1 auto;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  user-select: none;
  // Just to test without background : background-color: rgba(255, 255, 255, 0.8);
  & *:not(input textarea){
    user-select: none;
  }

  &__SelectionBox{
    position: absolute;
    top:0;
    left: 0;
    border: 1px solid tomato;
    background-color: rgba(tomato, .3);
    pointer-events: none;
    opacity: 1;
    &--hide{
      opacity: 0;
    }
  }
}

.node {
  width: 120px;
  padding: 10px;
  height: 100px;
  text-align: center;
  background: white;
  border: 2px solid #aaa;
  border-radius: 4px;
  position: absolute;
  top: 0;
  left: 0;
  &:focus{
    border-color: tomato;
    outline: none;
  }
  
  input{
    margin-top: 10px;
    width: 100%;
    border: 1px solid black;
  }
}

.node--selected{
  // z-index: 9999;
  // pointer-events: none;
  background-color: dodgerblue;
}

.node.hover{
  background: tomato;
}